@import "variables.scss";
@import "components.scss";

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  list-style: none;
  quotes: none;
  font-style: normal;
  outline: none;
  box-sizing: border-box;
}
*:focus {outline:none !important}
input:focus, textarea:focus, select:focus{
  outline: none;
}

html, body, #shell {
  background: $grey-1;
  color: $grey-4;
  overflow-y: scroll;
  cursor: default;
  -webkit-font-smoothing: antialiased;
  font-size: 13px;
  font-family: $base-font;
  display: block;
  height: 100%;
  @import "layout.scss";
}

@media (prefers-color-scheme: dark) {
  html, body, #shell {
      background: $grey-5;
      color: $white;
    }
}

@media (prefers-color-scheme: light) {
  html, body, #shell {
      background: $grey-1;
      color: $grey-4;
    }
}